<template>
  <video ref="player" :width="width" :height="height" class="video-js" data-setup="{}" v-bind="$attrs">
    <p class="vjs-no-js">
       video 
    </p>
  </video>
</template>

<script>
import videojs from 'video.js';
import 'videojs-flash';
import 'video.js/dist/video-js.min.css';
import video_zhCN from 'video.js/dist/lang/zh-CN.json';
// import SWF_URL from 'videojs-swf/dist/video-js.swf';
// videojs.options.flash.swf = SWF_URL;
videojs.addLanguage('zh-CN', video_zhCN);
//jsjsjson
//import  from '';
export default {
  name: 'hrm-player',
  inheritAttrs: false,
  //
  props: {
    source: String,
    type: {
      type: String,
      default: 'application/x-mpegURL',
    },
    width: {
      type: Number,
      default: 800,
    },
    height: {
      type: Number,
      default: 600,
    },
    options: {
      type: Object,
      default: () => {},
    },
  },
  //
  mixins: [],
  //import
  components: {},
  data() {
    //
    return {
      myPlayer: null,
      defaultOptions: {
        language: 'zh-CN',
        notSupportedMessage: '',
        ...this.options,
      },
    };
  },
  // data
  computed: {},
  //data
  watch: {
    source(val) {
      if (val) {
        this.changeSource(val, this.type);
      }
    },
  },
  //
  methods: {
    // 
    bindEvent() {
      if (this.myPlayer) {
        for (let listener in this.$listeners) {
          this.myPlayer.on(listener, this.$listeners[listener]);
        }
      }
    },
    // 
    initPlayer() {
      let _this = this;
      this.myPlayer = videojs(
        this.$refs.player,
        this.defaultOptions,
        function () {
          videojs.log('!');
          _this.bindEvent();
          if (_this.source) {
            _this.changeSource(_this.source, _this.type);
          }
        }
      );
    },
    // 
    changeSource(source, type) {
      if (this.myPlayer) {
        this.myPlayer.reset();
        this.myPlayer.src({
          src: source,
          type: type, // type
        });
        this.myPlayer.play();
      }
    },
    // 
    play(){
      if (this.myPlayer) {
        this.myPlayer.play();
      }
    },
    // 
    pause(){
      if (this.myPlayer) {
        this.myPlayer.pause();
      }
    },
    // 
    currentTime(){
      if (this.myPlayer) {
        return this.myPlayer.currentTime();
      }
    },
    // 
    setFullScreen(){
      if (this.myPlayer) {
        this.myPlayer.requestFullscreen();
      }
    },
    // 
    reset() {
      if (this.myPlayer) {
        this.myPlayer.reset();
      }
    },
    // 
    dispose() {
      if (this.myPlayer) {
        this.myPlayer.dispose();
        this.myPlayer = null;
      }
    },
  },
  // - this
  created() {},
  // - DOM
  mounted() {
    this.initPlayer();
  },
  // - 
  beforeCreate() {},
  // - 
  beforeMount() {},
  // - 
  beforeUpdate() {},
  // - 
  updated() {},
  // - 
  beforeDestroy() {},
  // - 
  destroyed() {
    this.dispose();
  },
  //keep-alive
  activated() {},
};
</script>

<style lang='scss'>
.video-js .vjs-big-play-button {
  display: none;
}
</style>